@import url(//fonts.googleapis.com/earlyaccess/jejugothic.css);

.Movie{
  width: 40%;
  height: 100%;
  min-width: 350px;
  min-height: 200px;
  padding: 30px 20px;
  margin: 30px 30px;
  position: relative;
  display: inline-table;
  background-color: white;
  -webkit-box-shadow:  0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
          box-shadow:  0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}
.Movie_col{
  display: inline-block;
  width: 90%;
  min-width: 30px;
  max-width: 300px;
  font-size: 0.8em;
  vertical-align: middle;
  margin: auto 10px;
  

}
.synopsis{
  font-size: 0.9;
  color: #646464;
  font-family: 'Jeju Gothic', sans-serif;
}
img{
  width: 100%;
}
.Movie_poster{
  width: 30%;
  margin: auto 10px;
  vertical-align: middle;
  display: inline-block;
  
}

@media (max-width: 1231px) {
  .Movie_poster{
    width: 60%;
  }
}

td{
  font-family: 'Jeju Gothic', sans-serif;
  display: inline-block;
  margin: auto 3px;
  color: #424242;
  font-size: 1.15em;
}
tbody{
  display: inline-block;
}
table{
  margin: auto;
  width: 100%;
  margin-bottom: 15px;
  display: inline-block;

}
.Movie_genres{

  width: 100%;
}


